import '../scss/style.scss';

if (window.innerWidth < 768){
  const mySwiper = new Swiper('.swiper', {
      slidesPerView: 1,
      slideClass: 'swiper-slide',
      pagination: {
           el: '.swiper-pagination',
          clickable: true,
            }
  });}
  
  if (window.innerWidth >= 768) {
      let swiper = document.querySelectorAll('.swiper');
      let swiperWrap = document.querySelectorAll('.swiper-wrapper');
      let swiperSlide = document.querySelectorAll('.swiper-slide');
      let swiperPagination = document.querySelectorAll('.swiper-pagination')
      
      for (let i = 0; i < swiper.length; i++){
        swiper[i].classList.toggle('swiper');
      }

      for (let i = 0; i < swiperWrap.length; i++){
        swiperWrap[i].classList.toggle('swiper-wrapper');
      }

      for (let i = 0; i < swiperSlide.length; i++){
        swiperSlide[i].classList.toggle('swiper-slide');
      }

      for (let i = 0; i < swiperPagination.length; i++){
        swiperPagination[i].classList.toggle('swiper-pagination');
      }

    }
  
  
    let close1120 = document.querySelectorAll('.card-close--1120');
    let button = document.querySelector('.read-more__a');
    let icon = document.querySelector('.read-more__icon--close');
    let close768 = document.querySelectorAll('.card-close--768');

    button.addEventListener('click', function(evt){
    evt.preventDefault();
    for(let i = 0; i < close1120.length; i++){
    close1120[i].classList.toggle('card-close--1120');};
    for(let i = 0; i < close768.length; i++){
    close768[i].classList.toggle('card-close--768');};
    button.innerHTML =
    (button.innerHTML === 'Показать все') ? button.innerHTML = 'Скрыть' : button.innerHTML = 'Показать все';
    icon.classList.toggle('read-more__icon--close')});
      //-------------------------------------------------------------------

    let button_2 = document.querySelector('.read-more__a-2');
    let close1120_2 = document.querySelectorAll('.card-close--1120-2');
    let close768_2 = document.querySelectorAll('.card-close--768-2');
    let icon_2 = document.querySelector('.read-more__icon--close-2');

    button_2.addEventListener('click', function(evt){
      evt.preventDefault();
      for(let i = 0; i < close1120_2.length; i++){
      close1120_2[i].classList.toggle('card-close--1120-2');};
      for(let i = 0; i < close768_2.length; i++){
      close768_2[i].classList.toggle('card-close--768-2');};
      button_2.innerHTML =
      (button_2.innerHTML === 'Показать все') ? button_2.innerHTML = 'Скрыть' : button_2.innerHTML = 'Показать все';
      icon_2.classList.toggle('read-more__icon--close-2')});
